dialog+.backdrop,
/* polyfill */
dialog::backdrop {
    /* native */
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
}

.modal-heading {
    margin: 2rem;
}

.modal-heading__text {
    padding: 2%;
    text-align: center;
}

.modal-heading__text:after,
.modal-heading__text:before {
    animation: animate-shapes 4s ease-in infinite;
}

.modal-heading__text:after {
    animation-delay: 1.5s;
}

.modal-content {
    background: wheat;
    border-radius: 5px;
    line-height: 2.5;
    padding: 1rem;
    margin: 3rem;
    color: navy;
    box-shadow: 0 0 23rem -3rem;
    border: inset darkcyan 1rem;
}

.modal-btn__close {
    background: #ff0;
    color: #00f;
}

.modal-btn__close:hover {
    background: #00f;
    color: #FF0;
}

@keyframes animate-shapes {
    30% {
        transform: rotate3d(1, 1, 1, 360deg) translate3d(10px, 10px, 10px);
    }

    70% {
        transform: rotate3d(1, 1, 1, 180deg) translate3d(-10px, -10px, -10px) scale3d(1.5, 1.5, 1.5);
    }
}