.coordinates-container {
  background: linear-gradient(#00f, #f00, #ff0);
  padding: 0.2rem;
  border-radius: 5px;
  margin: 0 3rem 2rem;
  box-shadow: 0 0 5px;
  display: inline-block;
  text-align: justify;
  min-width: 35%;
}

h3 {
  margin: 0.4rem;
  color: #fff;
}

.coordinates-content {
  margin: 0;
  color: green;
  padding: 1rem;
  text-shadow: 0 0 2px navy, 0 0 1px greenyellow;
  background: #000;
  border-radius: 5px;
  letter-spacing: 3px;
}

ul {
  list-style-type: none;
  display: inline-block;
}

ul:last-child {
  vertical-align: top;
}