dialog::-webkit-backdrop {
    /* native */
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
}

dialog+.backdrop,
/* polyfill */
dialog::backdrop {
    /* native */
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
}

.modal-heading {
    margin: 2rem;
}

.modal-heading__text {
    padding: 2%;
    text-align: center;
}

.modal-heading__text:after,
.modal-heading__text:before {
    -webkit-animation: animate-shapes 4s ease-in infinite;
            animation: animate-shapes 4s ease-in infinite;
}

.modal-heading__text:after {
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;
}

.modal-content {
    background: wheat;
    border-radius: 5px;
    line-height: 2.5;
    padding: 1rem;
    margin: 3rem;
    color: navy;
    box-shadow: 0 0 23rem -3rem;
    border: inset darkcyan 1rem;
}

.modal-btn__close {
    background: #ff0;
    color: #00f;
}

.modal-btn__close:hover {
    background: #00f;
    color: #FF0;
}

@-webkit-keyframes animate-shapes {
    30% {
        -webkit-transform: rotate3d(1, 1, 1, 360deg) translate3d(10px, 10px, 10px);
                transform: rotate3d(1, 1, 1, 360deg) translate3d(10px, 10px, 10px);
    }

    70% {
        -webkit-transform: rotate3d(1, 1, 1, 180deg) translate3d(-10px, -10px, -10px) scale3d(1.5, 1.5, 1.5);
                transform: rotate3d(1, 1, 1, 180deg) translate3d(-10px, -10px, -10px) scale3d(1.5, 1.5, 1.5);
    }
}

@keyframes animate-shapes {
    30% {
        -webkit-transform: rotate3d(1, 1, 1, 360deg) translate3d(10px, 10px, 10px);
                transform: rotate3d(1, 1, 1, 360deg) translate3d(10px, 10px, 10px);
    }

    70% {
        -webkit-transform: rotate3d(1, 1, 1, 180deg) translate3d(-10px, -10px, -10px) scale3d(1.5, 1.5, 1.5);
                transform: rotate3d(1, 1, 1, 180deg) translate3d(-10px, -10px, -10px) scale3d(1.5, 1.5, 1.5);
    }
}
header {
  margin-top: -1.4rem;
  height: 100%;
  padding: 1rem;
  margin-bottom: 2rem;
  box-shadow: 0 0 4px #000;
  display: flow-root;
  /* fallback */
  overflow: auto;
}

.brand-title {
  float: left;
}

nav {
  margin-top: 2rem;
  float: right;
}

.open-modal-btn {
  cursor: pointer;
  font-size: 2rem;
  -webkit-transition: 500ms;
  transition: 500ms;
  /* resetting button default style */
  background: transparent;
  border: none;
}

.open-modal-btn:hover {
  text-shadow: -1px -1px 1px #00f;
}

.modal-heading__text,
.brand-title__text {
  text-shadow: 0 0 2px #00f, 0 0 2px red;
  position: relative;
  display: inline-block;
}

.modal-heading__text:after,
.modal-heading__text:before,
.brand-title__text:after,
.brand-title__text:before {
  content: "";
  position: absolute;
  width: 40px;
  height: 40px;
  border: 2px solid;
  box-shadow: 0 0 3px, 1px 1px 5px;
}

.modal-heading__text:before,
.brand-title__text:before {
  color: #00f;
  right: 0;
  bottom: -15px;
  -webkit-transform: skewX(30deg);
          transform: skewX(30deg);
}

.modal-heading__text:after,
.brand-title__text:after {
  color: #ff0;
  border-radius: 50%;
  left: 0px;
  top: 0px;
}
footer {
  text-align: center;
  background: linear-gradient(45deg, #f00, #00f);
  line-height: 100px;
  color: whitesmoke;
  text-shadow: 1px 1px 4px #000;
  position: fixed;
  bottom: 0;
  width: 100vw;
}
.coordinates-container {
  background: -webkit-gradient(linear, left top, left bottom, from(#00f), color-stop(#f00), to(#ff0));
  background: linear-gradient(#00f, #f00, #ff0);
  padding: 0.2rem;
  border-radius: 5px;
  margin: 0 3rem 2rem;
  box-shadow: 0 0 5px;
  display: inline-block;
  text-align: justify;
  min-width: 35%;
}

h3 {
  margin: 0.4rem;
  color: #fff;
}

.coordinates-content {
  margin: 0;
  color: green;
  padding: 1rem;
  text-shadow: 0 0 2px navy, 0 0 1px greenyellow;
  background: #000;
  border-radius: 5px;
  letter-spacing: 3px;
}

ul {
  list-style-type: none;
  display: inline-block;
}

ul:last-child {
  vertical-align: top;
}
* {
  box-sizing: border-box;
}

body {
  height: 100vh;
  margin: 0;
  font-family: sans-serif;
}

#root {
  background-color: #ddd;
  text-align: center;
}

.shapes-btn {
  padding: 10px;
  margin: 1rem;
  border: 1px solid;
  font-weight: 900;
  text-transform: uppercase;
  -webkit-transition: all 500ms ease-in;
  transition: all 500ms ease-in;
}

#about {
  background: #ccc;
  max-width: 50%;
  min-height: 50%;
  padding: 2rem;
}

.konvajs-content {
  background: rgba(0, 0, 0, 0.9);
}
